@import url('https://fonts.googleapis.com/css2?family=Catamaran:wght@100;400;500&display=swap');


:root {
    --text: #2a2a2adf;
    --text-white: #FFFFFF;
    --main-color: #1a5638;
    --main-light-color: #5cb8a4;
   /* --contrast-color: #782078;
    --contrast-ligth-color: #EB9781;*/
    --bg-color: #f5f4f4;
    --font: 'Catamaran', sans-serif;
}

* {
    margin: 0;
    padding: 0;
    /*box-sizing: border-box;*/
    font-family: var(--font);
}

html {
    height: 100%;
    width: 100%;
}

.container {
    margin: 20px auto;
    max-width: 850px;
    box-sizing: border-box;
}

.logo{
    width: 30px;
}


/*Im√°genes*/
.cardIMG{
    margin: 5px auto;
    max-width: 500px;
    text-align: center;
    background-color: var(--bg-color);
}

img{
    width: 95%;
    margin: 5px auto;
}

p{
    margin-top: 30px;
}

.list-cat {
    list-style: none;
    text-align: center;
}

.list-cat li{
    border: 2px solid var(--main-light-color);
    display: inline-block;
    width: 150px;
    height: 130px;
    margin: 0px 10px;
    border-radius: 10px;
    color: var(--main-color);
}

.list-cat li:hover{
    background-color: #5cb8a4;
    color: var(--text-white);
    border-radius: 50px 10px 50px 10px;
    font-weight: 500;
}

.list-cat li img{
    width: 50%;
    margin: 10px auto;
    display: block;

}




/*Breadcrumb*/
.breadcrumb ul {
    padding: 10px 16px;
    list-style: none;
  }
  .breadcrumb li {
    display: inline;
    font-size: 14px;
  }
  .breadcrumb  li a {
    color: var(--main-light-color);
    text-decoration: none;
  }

  .breadcrumb li+li:before {
    padding: 8px;
    color: var(--main-light-color);
    content: "|";
  }