@import "https://fonts.googleapis.com/css2?family=Catamaran:wght@100;400;500&display=swap";
:root {
  --text: #2a2a2adf;
  --text-white: #fff;
  --main-color: #1a5638;
  --main-light-color: #5cb8a4;
  --bg-color: #f5f4f4;
  --font: "Catamaran", sans-serif;
}

* {
  font-family: var(--font);
  margin: 0;
  padding: 0;
}

html {
  height: 100%;
  width: 100%;
}

.container {
  max-width: 850px;
  box-sizing: border-box;
  margin: 20px auto;
}

.logo {
  width: 30px;
}

.cardIMG {
  max-width: 500px;
  text-align: center;
  background-color: var(--bg-color);
  margin: 5px auto;
}

img {
  width: 95%;
  margin: 5px auto;
}

p {
  margin-top: 30px;
}

.list-cat {
  text-align: center;
  list-style: none;
}

.list-cat li {
  border: 2px solid var(--main-light-color);
  width: 150px;
  height: 130px;
  color: var(--main-color);
  border-radius: 10px;
  margin: 0 10px;
  display: inline-block;
}

.list-cat li:hover {
  color: var(--text-white);
  background-color: #5cb8a4;
  border-radius: 50px 10px;
  font-weight: 500;
}

.list-cat li img {
  width: 50%;
  margin: 10px auto;
  display: block;
}

.breadcrumb ul {
  padding: 10px 16px;
  list-style: none;
}

.breadcrumb li {
  font-size: 14px;
  display: inline;
}

.breadcrumb li a {
  color: var(--main-light-color);
  text-decoration: none;
}

.breadcrumb li + li:before {
  color: var(--main-light-color);
  content: "|";
  padding: 8px;
}

